<template><section><h1>图标 Icon</h1>
<h2>基础用法 [{{icons.length}}]</h2>
<p>AsUI 的所有图标全部采用字体形式，取材于阿里巴巴矢量图标库（iconfont）。因此你可以把一个 icon 看作是一个普通的文字，这意味着你直接用 css 控制文字属性，如 color、font-size，就可以改变图标的颜色和大小。</p>
<demo-block>
                            <div class="source" slot="source">
  <div class="demo-block">
    <div class="icon-item" v-copy="item" v-copy:callback="handleCopy" v-for="(item, index) in icons" :key="index">
      <as-icon :name="item"></as-icon>
      <label>{{item}}</label>
    </div>
  </div>

  

  
</div>
