</section></template>
<style lang="scss">
  .icon-item {
    display: inline-block;
    text-align: center;
    width: 90px;
    height: 90px;
    .as-icon {
      font-size: 30px;
      transition: transform .3s ease-in;
      &:hover {
        cursor: pointer;
        transform: scale(2);
      }
    }
    label {
      display: block;
      margin-top: 16px;
    }
  }
  </style>
<script>
    const iconList = [
      'success', 'info', 'warning', 'error','closed', 'help', 'checked', 'check','close', 'goods', 'cart', 'service', 'flag', 'survey', 'document', 'book', 'ticket', 'list', 'table', 'time', 'date', 'bell', 'voice', 'fire', 'users', 'user', 'circle', 'phone', 'password', 'view', 'email', 'message', 'love', 'loved', 'location',  'chat', 'upload', 'download', 'minus', 'add', 'edit', 'back', 'arrowup', 'arrowdown', 'left', 'right', 'down', 'up','prev', 'next', 'chart', 'piechart', 'linechart', 'sexw', 'sexm', 'star', 'stared', 'like',  'liked', 'fullscreen', 'outscreen', 'tag', 'webpack', 'camera','picture', 'video', 'pause', 'play', 'sort', 'menu','more', 'top', 'refresh', 'loading', 'setting', 'delete',  'share', 'dollar', 'search', 'zoom', 'code', 'home'
    ];
    export default {
      data() {
        return {
          icons: iconList
        };
      },
      methods: {
        handleCopy(value) {
          alert(`Copied: ${value}`);
        }
      }
    };
  </script>


